import React, { useEffect, useState } from 'react';
import { Navbar, Nav, Container } from 'react-bootstrap';
import RunnerlyLogo from '../assets/img/gallery/RunnerlyLogo.png';
import { Link, useNavigate } from 'react-router-dom';

const NavbarComp = () => {
  const navigate = useNavigate();
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    const navLinks = document.querySelectorAll('.nav-link');
    const navLinksBefore = document.querySelectorAll('.nav-link::before');

    navLinks.forEach((link, index) => {
      link.style.animationDelay = `${index * 0.1 + 0.3}s`;
    });

    navLinksBefore.forEach((link, index) => {
      link.style.animationDelay = `${index * 0.1 + 0.3}s`;
    });
  }, []);

  const handleUserIconClick = () => {
    if (isLoggedIn) {
      // Handle logout action
      setIsLoggedIn(false);
      // Redirect user to homepage or any other page
      navigate('/');
    } else {
      // Redirect user to login page
      navigate('/login');
    }
  };

  return (
    <Navbar style={{ backgroundColor: '#FF6A13', position: 'fixed', top: 0, left: 0, right: 0, zIndex: 100 }} expand="lg" className="d-flex align-items-center justify-content-between">
      <Container>
        <Navbar.Brand as={Link} to="/" className="d-flex align-items-center">
          <img
            src={RunnerlyLogo}
            width="100px"
            height="25px"
            className="d-inline-block align-top"
            alt="Runnerly Logo"
          />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link as={Link} to="/" className="nav-link" style={{ marginRight: '20px' }}>
              <h1>Home</h1>
            </Nav.Link>
            <Nav.Link as={Link} to="/login" className="nav-link">
              <h1>About</h1>
            </Nav.Link>
          </Nav>
          <Nav>
          <Nav.Link as={Link} to="/login" className="nav-link" style={{ marginLeft: '20px' }}>
  <i className="fas fa-user footer-user-icon" onClick={handleUserIconClick}></i>
</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default NavbarComp;
