import React, { useEffect, useState } from 'react';
import { Navbar, Nav, Container, Card, Collapse } from 'react-bootstrap';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import axios from 'axios';

const Dashboard = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [cards, setCards] = useState([]);
  const [isNavbarOpen, setIsNavbarOpen] = useState(false);

  useEffect(() => {
    setIsLoggedIn(location.pathname !== '/login');
  }, [location.pathname]);

  useEffect(() => {
    if (isLoggedIn) {
      // Lakukan pemanggilan API untuk mendapatkan data card
      axios
        .get('https://fantastic-bat-hose.cyclic.app/')
        .then((response) => {
          setCards(response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }, [isLoggedIn]);

  const handleUserIconClick = () => {
    if (isLoggedIn) {
      setIsLoggedIn(false);
      navigate('/');
    } else {
      navigate('/login');
    }
  };

  const handleNavbarToggle = () => {
    setIsNavbarOpen(!isNavbarOpen);
  };

  return (
    <div>
      {/* Navbar */}
      <Navbar
        style={{ backgroundColor: '#FF6A13', position: 'fixed', top: 0, left: 0, right: 0, zIndex: 100 }}
        expand="lg"
      >
        <Container>
          <Navbar.Brand as={Link} to="/" className="d-flex align-items-center">
            Dashboard
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" onClick={handleNavbarToggle} />
          <Collapse in={isNavbarOpen}>
            <div>
              <Nav className="mr-auto flex-column">
                <Nav.Link as={Link} to="/" className="nav-link">
                  Home
                </Nav.Link>
                <Nav.Link as={Link} to="/login" className="nav-link">
                  About
                </Nav.Link>
              </Nav>
              <Nav>
                {isLoggedIn && (
                  <Nav.Link as={Link} to="/" className="nav-link" style={{ marginLeft: '20px' }}>
                    <i className="fas fa-user footer-user-icon" onClick={handleUserIconClick}></i>
                  </Nav.Link>
                )}
              </Nav>
            </div>
          </Collapse>
        </Container>
      </Navbar>

      {/* Content */}
      <Container className="mt-5">
        <h1>Welcome to Dashboard</h1>
        <div className="row mt-5">
          {cards.map((card) => (
            <div key={card.id} className="col-md-4 mb-4">
              <Card>
                <Card.Body>
                  <Card.Title>{card.title}</Card.Title>
                  <Card.Text>{card.description}</Card.Text>
                </Card.Body>
              </Card>
            </div>
          ))}
        </div>
      </Container>
    </div>
  );
};

export default Dashboard;
